<template>
  <div :style="{ direction: $vs.rtl ? 'rtl' : 'ltr' }">
    <feather-icon
      icon="Edit3Icon"
      svgClasses="h-5 w-5 mr-4 hover:text-primary cursor-pointer"
      @click.prevent="getDataFromParams"
      @click="popupActive2 = true"
    />
    <feather-icon
      icon="Trash2Icon"
      svgClasses="h-5 w-5 hover:text-danger cursor-pointer"
      @click="confirmDeleteRecord"
    />
    <vs-popup
      classContent="popup-example"
      title="Formulaire de modification"
      :active.sync="popupActive2"
    >
      <div class="vx-row mb-6">
        <div class="vx-col sm:w-1/3 w-full">
          <span>Nom</span>
        </div>
        <div class="vx-col sm:w-2/3 w-full">
          <vs-input
            class="w-full"
            icon-pack="feather"
            icon="icon-user"
            icon-no-border
            v-model="user.first_name"
          />
        </div>
      </div>
      <div class="vx-row mb-6">
        <div class="vx-col sm:w-1/3 w-full">
          <span>Prénom</span>
        </div>
        <div class="vx-col sm:w-2/3 w-full">
          <vs-input
            class="w-full"
            icon-pack="feather"
            icon="icon-user"
            icon-no-border
            v-model="user.last_name"
          />
        </div>
      </div>
      <div class="vx-row mb-6">
        <div class="vx-col sm:w-1/3 w-full">
          <span>Nom d'utilisateur</span>
        </div>
        <div class="vx-col sm:w-2/3 w-full">
          <vs-input
            class="w-full"
            icon-pack="feather"
            icon="icon-user"
            icon-no-border
            v-model="user.username"
          />
        </div>
      </div>
      <div class="vx-row mb-6">
        <div class="vx-col sm:w-1/3 w-full">
          <span>Email</span>
        </div>
        <div class="vx-col sm:w-2/3 w-full">
          <vs-input
            type="email"
            class="w-full"
            icon-pack="feather"
            icon="icon-mail"
            icon-no-border
            v-model="user.email"
          />
        </div>
      </div>
      <div class="vx-row mb-6">
        <div class="vx-col w-full">
          <vs-checkbox v-model="user.is_staff">Commercial</vs-checkbox>
        </div>
      </div>
      <div class="vx-row mb-6">
        <div class="vx-col w-full">
          <vs-checkbox color="success" v-model="user.is_superuser">Administrateur</vs-checkbox>
        </div>
      </div>
      <div class="vx-row mb-6">
        <div class="vx-col w-full">
          <vs-checkbox color="success" v-model="user.is_active">Actif</vs-checkbox>
        </div>
      </div>
      <div class="vx-row">
        <div class="vx-col sm:w-2/3 w-full ml-auto">
          <vs-button class="mr-3 mb-2" @click="editRecord">Modifier</vs-button>
          <vs-button color="warning" type="border" class="mb-2" @click="popupActive2 = false">Annuler</vs-button>
        </div>
      </div>
    </vs-popup>
  </div>
</template>

<script>
import axios from "axios";


export default {
  name: "CellRendererActions",
  data() {
    return {
      componentKey: 0,
      popupActive2: false,
      user: {
        first_name: "",
        last_name: "",
        email: "",
        username: "",
        is_staff: "",
        is_superuser: "",
        password: "",
        is_active: ""
      },
    };
  },
  methods: {
    getDataFromParams() {
      (this.user.first_name = this.params.data.first_name),
        (this.user.last_name = this.params.data.last_name),
        (this.user.username = this.params.data.username),
        (this.user.email = this.params.data.email),
        (this.user.is_staff = this.params.data.is_staff),
        (this.user.is_superuser = this.params.data.is_superuser);
      (this.user.is_active = this.params.data.is_active);
      (this.user.password = this.params.data.password);
    },
    confirmDeleteRecord() {
      this.$vs.dialog({
        type: "confirm",
        color: "danger",
        title: `Confirmation de la suppression`,
        text: `Supprimer "${this.params.data.first_name} ${this.params.data.last_name}"`,
        accept: this.deleteRecord,
        acceptText: "Supprimer",
        cancelText: "Annuler",
      });
    },
    async deleteRecord() {
      await axios
        .delete(`/users/${this.params.data.id}/`, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.showDeleteSuccess();
        });
    },
    showDeleteSuccess() {
      this.$vs.notify({
        color: "success",
        title: "Utilisateur supprimé",
      });
    },
    async editRecord() {
      await axios
        .put(`/users/${this.params.data.id}/`, this.user, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.showEditSuccess();
          this.popupActive2 = false
          this.Getusers
        })
        .catch(() => {
          this.$vs.notify({
            title: 'Erreur de modification',
            text: 'veuillez vérifier vos informations',
            iconPack: 'feather',
            icon: 'icon-alert-circle',
            color: 'danger'
          })
        });

    },
    showEditSuccess() {
      this.$vs.notify({
        color: "success",
        title: "Utilisateur modifié",
      });
    },

  },
};
</script>
